<template>
  <div id="userLayout">
    <div class="login_panle">
      <div class="login_panle_form">
        <div class="login_panle_form_title">
          <p class="login_panle_form_title_p">Ê≥®ÂÜåË¥¶Âè∑</p>
        </div>
        <el-form
          ref="loginForm"
          :model="loginForm"
          :rules="rules"
          @keyup.enter="submitForm"
        >
          <el-form-item prop="username">
            <el-input v-model="loginForm.username" placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç">
              <template #suffix>
                <i class="el-input__icon el-icon-user" />
              </template>
            </el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input
              v-model="loginForm.password"
              :type="lock === 'lock' ? 'password' : 'text'"
              placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"
            >
              <template #suffix>
                <i
                  :class="'el-input__icon el-icon-' + lock"
                  @click="changeLock"
                />
              </template>
            </el-input>
          </el-form-item>
          <el-form-item prop="correctPassword">
            <el-input
              v-model="loginForm.correctPassword"
              :type="lock === 'lock' ? 'password' : 'text'"
              placeholder="ËØ∑Á°ÆËÆ§ÂØÜÁ†Å"
            >
              <template #suffix>
                <i
                  :class="'el-input__icon el-icon-' + lock"
                  @click="changeLock"
                />
              </template>
            </el-input>
          </el-form-item>

          <!-- <el-form-item style="position: relative" prop="captcha">
            <el-input
              v-model="loginForm.captcha"
              name="logVerify"
              placeholder="ËØ∑ËæìÂÖ•È™åËØÅÁ†Å"
              style="width: 60%"
            />
            <div class="vPic">
              <img
                v-if="picPath"
                :src="picPath"
                alt="ËØ∑ËæìÂÖ•È™åËØÅÁ†Å"
                @click="loginVerify()"
              >
            </div>
          </el-form-item> -->
          <el-form-item style="">
            <el-button type="primary" style="width: 46%;" @click="toLogin"
              >ËøîÂõûÁôªÂΩï</el-button
            >
            <el-button
              type="primary"
              style="width: 46%; margin-left: 8%"
              @click="submitForm"
              >Ê≥® ÂÜå</el-button
            >
          </el-form-item>
        </el-form>
      </div>
      <div class="login_panle_right" />
      <div class="login_panle_foot">
        <div class="copyright">Copyright &copy; {{ curYear }} üíñ</div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions } from "vuex";
import { useRouter } from "vue-router";
import { register } from "@/api/user"

export default {
  name: "Login",
  setup() {
    const router = useRouter();
    function toLogin() {
      router.push("/login");
    }
    return {
      toLogin,
    };
  },
  data() {
    const checkUsername = (rule, value, callback) => {
      if (value.length < 5) {
        return callback(new Error("ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÁî®Êà∑Âêç"));
      } else {
        callback();
      }
    };
    const checkPassword = (rule, value, callback) => {
      if (value.length < 6) {
        return callback(new Error("ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÂØÜÁ†Å"));
      } else {
        callback();
      }
    };
    const checkCorrectPassword = (rule, value, callback) => {
      console.log(value);
      if (value !== this.loginForm.password) {
        return callback(new Error("‰∏§Ê¨°ÂØÜÁ†Å‰∏ç‰∏ÄËá¥"));
      } else {
        callback();
      }
    };
    return {
      curYear: 0,
      lock: "lock",
      loginForm: {
        username: "",
        password: "",
        correctPassword: "",
      },
      rules: {
        username: [{ validator: checkUsername, trigger: "blur" }],
        password: [{ validator: checkPassword, trigger: "blur" }],
        correctPassword: [{ validator: checkCorrectPassword, trigger: "blur" }],
      },
      logVerify: "",
      picPath: "",
    };
  },
  created() {
    this.curYear = new Date().getFullYear();
  },
  methods: {
    ...mapActions("user", ["LoginIn"]),
    async onRegister() {
      return await register(this.loginForm);
    },
    async submitForm() {
      this.$refs.loginForm.validate(async (v) => {
        if (v) {
          await this.onRegister();
        } else {
          this.$message({
            type: "error",
            message: "ËØ∑Ê≠£Á°ÆÂ°´ÂÜôÁôªÂΩï‰ø°ÊÅØ",
            showClose: true,
          });
          return false;
        }
      });
    },
    changeLock() {
      this.lock = this.lock === "lock" ? "unlock" : "lock";
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/style/newLogin.scss";
